@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400;700;900&display=swap);
*{padding:0;box-sizing:border-box;margin:0;line-height:150%}body{background:#fff;font-family:"Roboto Slab",serif}h1{font-size:42px;font-weight:900}h2{font-weight:700;font-size:32px}h3{font-weight:700;font-size:32px}h5{font-size:24px;font-weight:700}h6{font-size:18px}p{font-size:20px}.setPadding{padding:40px 2%}input,button{font-size:16px;outline:none;border:none;border-radius:5px;padding:10px}button{cursor:pointer;background:#85c441;color:#fff;font-size:16px;font-weight:700}input{width:60%;max-width:330px;background:#dbdbdb;color:#2f2f2f;font-weight:700}input ::-webkit-input-placeholder{color:#2f2f2f}input :-ms-input-placeholder{color:#2f2f2f}input ::-ms-input-placeholder{color:#2f2f2f}input ::placeholder{color:#2f2f2f}.input{width:100%;display:flex;justify-content:center;align-items:center}.input button{margin-left:20px}
